<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-popover title=\&quot;Title\&quot; trigger=\&quot;hover\&quot;&gt;\n    &lt;v-button&gt;Hover me&lt;/v-button&gt;\n    &lt;div slot=\&quot;content\&quot;&gt;\n        &lt;p&gt;Content&lt;/p&gt;\n        &lt;p&gt;Content&lt;/p&gt;\n    &lt;/div&gt;    \n&lt;/v-popover&gt;\n&lt;v-popover title=\&quot;Title\&quot; trigger=\&quot;focus\&quot;&gt;\n    &lt;v-button&gt;Focus me&lt;/v-button&gt;\n    &lt;div slot=\&quot;content\&quot;&gt;\n        &lt;p&gt;Content&lt;/p&gt;\n        &lt;p&gt;Content&lt;/p&gt;    \n&lt;/div&gt;&lt;/v-popover&gt;\n&lt;v-popover title=\&quot;Title\&quot; trigger=\&quot;click\&quot;&gt;\n    &lt;v-button&gt;Click me&lt;/v-button&gt;\n    &lt;div slot=\&quot;content\&quot;&gt;\n        &lt;p&gt;Content&lt;/p&gt;\n        &lt;p&gt;Content&lt;/p&gt;\n    &lt;/div&gt;    \n&lt;/v-popover&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-popover title="Title" trigger="hover">
    <v-button>Hover me</v-button>
    <div slot="content">
        <p>Content</p>
        <p>Content</p>
    </div>    
</v-popover>
<v-popover title="Title" trigger="focus">
    <v-button>Focus me</v-button>
    <div slot="content">
        <p>Content</p>
        <p>Content</p>    
</div></v-popover>
<v-popover title="Title" trigger="click">
    <v-button>Click me</v-button>
    <div slot="content">
        <p>Content</p>
        <p>Content</p>
    </div>    
</v-popover>
</div>
