<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-popover title=\&quot;Title\&quot; trigger=\&quot;hover\&quot; :controlled=\&quot;controlled\&quot;&gt;\n        &lt;v-button&gt;Hover me&lt;/v-button&gt;\n        &lt;div slot=\&quot;content\&quot;&gt;\n            &lt;p&gt;Content&lt;/p&gt;\n            &lt;p&gt;Content&lt;/p&gt;\n        &lt;/div&gt;\n    &lt;/v-popover&gt;\n    &lt;v-popover title=\&quot;Title\&quot; trigger=\&quot;focus\&quot; :controlled=\&quot;controlled\&quot;&gt;\n        &lt;v-button&gt;Focus me&lt;/v-button&gt;\n        &lt;div slot=\&quot;content\&quot;&gt;\n            &lt;p&gt;Content&lt;/p&gt;\n            &lt;p&gt;Content&lt;/p&gt;\n    &lt;/div&gt;&lt;/v-popover&gt;\n    &lt;v-popover title=\&quot;Title\&quot; trigger=\&quot;click\&quot; :controlled=\&quot;controlled\&quot;&gt;\n        &lt;v-button&gt;Click me&lt;/v-button&gt;\n        &lt;div slot=\&quot;content\&quot;&gt;\n            &lt;p&gt;Content&lt;/p&gt;\n            &lt;p&gt;Content&lt;/p&gt;\n        &lt;/div&gt;\n    &lt;/v-popover&gt;\n    &lt;v-button type=\&quot;primary\&quot; @click=\&quot;controlled = !controlled\&quot;&gt;设置controlled&lt;/v-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data: () =&gt; ({\n        controlled: false\n    })\n  }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-popover title="Title" trigger="hover" :controlled="controlled">
        <v-button>Hover me</v-button>
        <div slot="content">
            <p>Content</p>
            <p>Content</p>
        </div>
    </v-popover>
    <v-popover title="Title" trigger="focus" :controlled="controlled">
        <v-button>Focus me</v-button>
        <div slot="content">
            <p>Content</p>
            <p>Content</p>
    </div></v-popover>
    <v-popover title="Title" trigger="click" :controlled="controlled">
        <v-button>Click me</v-button>
        <div slot="content">
            <p>Content</p>
            <p>Content</p>
        </div>
    </v-popover>
    <v-button type="primary" @click="controlled = !controlled">设置controlled</v-button>
</template>

</div>
