<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;   \n    &lt;v-popover v-model=\&quot;visible\&quot; title=\&quot;Title\&quot; trigger=\&quot;click\&quot;&gt;\n        &lt;v-button type=\&quot;primary\&quot;&gt;Click me&lt;/v-button&gt;\n        &lt;a slot=\&quot;content\&quot; @click=\&quot;visible = false\&quot;&gt;Close&lt;/a&gt;    \n    &lt;/v-popover&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data: () =&gt; ({\n        visible: false\n    })\n  }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>   
    <v-popover v-model="visible" title="Title" trigger="click">
        <v-button type="primary">Click me</v-button>
        <a slot="content" @click="visible = false">Close</a>    
    </v-popover>
</template>

</div>
                  <div slot="description"><h4 id="cong-fu-ceng-nei-guan-bi"><a class="header-anchor" href="#cong-fu-ceng-nei-guan-bi" aria-hidden="true">¶</a> 从浮层内关闭</h4>
<p>使用 <code>visible</code> 属性控制浮层显示。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>   
    <span class="hljs-tag">&lt;<span class="hljs-name">v-popover</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"visible"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Title"</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"click"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Click me<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"visible = false"</span>&gt;</span>Close<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>    
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-popover</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> ({
        <span class="hljs-attr">visible</span>: <span class="hljs-literal">false</span>
    })
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
