<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h2 id="popover-props"><a class="header-anchor" href="#popover-props" aria-hidden="true">¶</a> Popover Props</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>init-visible</td>
<td>初始状态是否显示popover</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>卡片标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>placement</td>
<td>气泡框位置，可选 <code v-pre="">top</code> <code v-pre="">left</code> <code v-pre="">right</code> <code v-pre="">bottom</code> <code v-pre="">topLeft</code> <code v-pre="">topRight</code> <code v-pre="">bottomLeft</code> <code v-pre="">bottomRight</code> <code v-pre="">leftTop</code> <code v-pre="">leftBottom</code> <code v-pre="">rightTop</code> <code v-pre="">rightBottom</code></td>
<td>String</td>
<td>top</td>
</tr>
<tr>
<td>trigger</td>
<td>触发行为，可选 <code v-pre="">hover/focus/click</code></td>
<td>String</td>
<td>hover</td>
</tr>
<tr>
<td>overlay-style</td>
<td>卡片样式</td>
<td>String/Object</td>
<td>-</td>
</tr>
<tr>
<td>controlled</td>
<td>保持popover显示</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>title:slot</td>
<td>卡片标题，优先级高于title prop</td>
<td>Slot Node</td>
<td>-</td>
</tr>
<tr>
<td>content:slot</td>
<td>卡片内容</td>
<td>Slot Node</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="popover-events"><a class="header-anchor" href="#popover-events" aria-hidden="true">¶</a> Popover Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>visible发生变化时触发</td>
<td>visible</td>
</tr>
</tbody>
</table>
